import React, { PureComponent } from "react";
import { Helmet } from "react-helmet";
import { CloudUrl } from "../../../constants";

export default class Common extends PureComponent {
  render() {
    const { appData } = this.props;

    return (
      <>
        {this.props.font ? (
          <Helmet>
            <style>
              {`@font-face {
                      font-family: 'TaggboxSocialFonts';
                      src:url('${CloudUrl}/media/web-fonts/TaggboxSocialFonts4.ttf?var=2') format('truetype'),
                          url('${CloudUrl}/media/web-fonts/TaggboxSocialFonts4.woff?var=2') format('woff'),
                          url('${CloudUrl}/media/web-fonts/TaggboxSocialFonts4.svg?var=2') format('svg');
                      font-weight: normal;
                      font-style: normal;
                      font-display: swap;
                  }`}
            </style>
          </Helmet>
        ) : (
          <Helmet>
            <title>
              {appData.wall.isDisplay
                ? appData &&
                  appData.wall &&
                  Object.keys(appData.wall).length > 0 &&
                  appData.wall.Wall
                  ? `${appData.wall.Wall.name} | Live Socialwall - Taggbox`
                  : "Display"
                : "Widget"}
            </title>
          </Helmet>
        )}
      </>
    );
  }
}
